@import url("https://rsms.me/inter/inter-ui.css");
h2,
h3 {
  margin: 0;
  font-size: 24px;
  font-weight: normal; }

h3 {
  font-weight: bold; }

.header__logo {
  position: absolute;
  top: 24px;
  left: 24px;
  width: 101px;
  height: 35px; }

.header__background {
  margin-bottom: -20%;
  width: 100%; }

.header__intro {
  font-size: 24px; }

@media only screen and (min-width: 768px) {
  .header__container {
    max-width: 520px; }
  .header__logo {
    position: relative; }
  .header__background {
    position: absolute;
    top: 0;
    right: 0;
    width: 45%;
    max-width: 680px; }
  .header__title {
    margin-top: 170px; } }



