@import url("https://rsms.me/inter/inter-ui.css");
h2,
h3 {
  margin: 0;
  font-size: 24px;
  font-weight: normal; }

h3 {
  font-weight: bold; }

.collection__container {
  border-radius: 16px;
  background-color: #ffffff;
  padding: 24px;
  max-width: 600px;
  color: #191a1b; }

.collection__coin {
  font-weight: bold; }

.collection__ticker {
  margin-right: 6px; }

.collection__address {
  color: #ec1076; }

.collection__coins {
  margin: 16px 0; }

.collection__subtitle {
  margin: 40px 0; }



