@import url("https://rsms.me/inter/inter-ui.css");
h2,
h3 {
  margin: 0;
  font-size: 24px;
  font-weight: normal; }

h3 {
  font-weight: bold; }

.pledge__title {
  margin: 0.9em 0; }

.pledge__notice {
  margin-top: 0.7em;
  color: #888888;
  font-size: 12px; }

@media only screen and (min-width: 768px) {
  .pledge {
    max-width: 500px; } }



